.card{
    padding: 2rem;
}
.wrap{
    display: flex;
    flex-wrap: wrap;
}
.wrap img{
    width: 20%;
    margin: 10px 5px;
    border-radius: 5px;
    object-fit:cover;
    
}
.card-body .card-title img{
    width: 40px;
    height: 40px;
    margin-right: 10px;
    border-radius: 50%;
    object-fit:cover;
}

.card-title{
    display: flex;
}