div .text-bold{
    font-weight: var(--font-weight-bold);
    font-family: var(--font-family-primary);
    font-size: 1.4rem;
    color: #deab3a;
    padding: 0;
    margin: 0;
}

div .text-title{
    font-weight: var(--font-weight-medium);
    font-family: var(--font-family-primary);
    font-size: 0.8rem;
    color: var(--color-primary);
}
div .text-blacks{
    font-weight: var(--font-weight-bold);
    font-family: var(--font-family-primary);
    font-size: 0.8rem;
    color: #FFC857;
}

div .text-mini{
    font-weight: var(--font-weight-normal);
    font-family: var(--font-family-primary);
    font-size: 0.8rem;
    color: var(--color-light);
    letter-spacing: 0.5px;
    padding: 0;
    margin: 0;
    line-height: 19px;
}

div .text-mini-black{
    font-weight: var(--font-weight-normal);
    font-family: var(--font-family-primary);
    font-size: 0.8rem;
    color: var(--color-text);
    letter-spacing: 0.5px;
    padding: 0;
    margin: 0;
    line-height: 19px;
}
