.submit{
    position: fixed;
    right: 0;
    bottom: 0px;
    width: 100%;
    height: 8%;
    border-top-right-radius: var(--border-primary);
    border-top-left-radius: var(--border-primary);
    border: 0;
    font-family: var(--font-family-primary);
    font-weight: var(--font-weight-medium);
    font-size: 2rem;
    letter-spacing: 3px;
    background-color: rgba(1, 206, 1, 0.952);
    color: var(--color-light);
    z-index: 10;
}
#liveAlertPlaceholder{
    z-index: 11;
    position: fixed;
    right: 1px;
    bottom: 10rem;
    /* display: none; */
    
}

#btn{
    background-color: #178C57;
    color:  rgba(255, 255, 255, 0.952);
}

