:root{
    --color-dark: #2B2B2B          ;
    --color-darkest: #3b3b3b     ;
    --color-light: #e0e0e0 ;
    --color-primary:#FFFFFF      ;
    

    --padding-primary: var(--distance-primary);
    --distance-primary: 30px;
    --margin-primary: var(--distance-primary);
    --border-primary: calc(var(--distance-primary)/2);
    
    --color-bg-body: var(--color-darkest:#8D77FE ;)

    --font-weight-medium: 500;
    --font-weight-medium: 500;
    --font-weight-bold: 700;
    --font-weight-normal: 300;
    --font-family-primary:'Poppins', sans-serif;
    
    --color-text:#000
}
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;500;700&family=Roboto:wght@400;700&family=Secular+One&display=swap');
.titleBetween{
    display:flex ;
    justify-content: space-between;
}

.doctor{
    width: 50px;
    height: 50px;
}